:root {
  --text-light: hsl(0, 0%, 10%);
  --text-lighter: hsl(0, 0%, 70%);
  --text-lighterer: hsl(0, 0%, 95%);
  --bg: hsl(0, 0%, 100%);
  --bg-dark: hsl(0, 0%, 65%);
  --focus: hsl(0, 60%, 20%);

  font-size: 1.5rem;
}
body {
  box-sizing: border-box;
  background-color: var(--bg);
  color: var(--text-light);
  box-sizing: border-box;
}
.container {
  padding: 1rem;
}
ul {
  padding: 0;
}
form {
  position: relative;
}
input {
  box-sizing: border-box;
  width: 100%;

  padding: 0.2rem 0.5rem;
  border-radius: 1rem;
  font-size: 1rem;
  color: var(--text-light);
  box-shadow: 0 0.05rem 0.5rem 0.01rem var(--text-lighter);
  box-shadow: inset 0 0.05rem 0.1rem 0.1rem var(--text-lighter);
  border: none;
  outline: none;
}
:focus {
  outline: 0;
  box-shadow: inset 0 0.05rem 0.1rem 0.1rem var(--focus) !important;
}
.input_button {
  width: 4rem;
  height: 100%;
  position: absolute;
  top: 0px;
  right: 0px;
  box-shadow: inset -0.15rem 0rem 0.1rem 0.1rem var(--text-lighter);
}


button {
  cursor: pointer;
  border-radius: 1rem;
  font-size: 1rem;
  border: none;
}

.note {
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  background-color: var(--bg-light);

  list-style: none;
  padding-top: 0.5rem;
  margin: 0.5rem 0;
  box-shadow: 0 -1px var(--text-lighter);
}
.note_span {
  padding: 0.1rem 0.5rem;
  background-color: var(--text-lighterer);
  border-radius: 1rem;
  justify-self: start;
}
.word_wrapper {
  flex-grow: 1;
  display: inline-block;
  word-wrap: wrap;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 5px;
}
.note button {
  border-radius: 100%;
  font-size: 1rem;
  width: 1.3rem;
  height: 1.3rem;
}
